.modal {
    height: 100vh;
    width: 100vw;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    opacity: 0;
    pointer-events: none;
}

.modal.active{
opacity: 1;
transform: scale(1);
pointer-events: all;
}

.modal__content {
    padding: 20px;
    background-color: white;
    width: 50vw;
    color: black;
    
}